body {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

/* HEADER DESIGN */

.nav-link {
    color: black;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.offer-contain h1 {
    text-align: center;
}


.fa-circle-check {
    margin-right: 10px;
    color: green;
}

.fa-cart-plus {
    margin-right: 10px;
}

/* HEADER DESIGN */

/* HOME DESIGN */

.discover-row h2 {
    text-align: center;
    font-size: 3.5rem;
}

.discover-link {
    font-size: 1.12rem;
}


.row-banner {
   display: flex;
   align-items: center;
   justify-content: center;
   

   
}

.row-banner > p {
    position: absolute;
    font-size: 5rem;
    color: #fff; 
    z-index: 1;
    
} 

.life-style {
    position: relative;
    height: 45rem;
    width: 100%;
    object-fit: cover;
    filter: brightness(70%);
    
}

.evaluare-img {
    object-fit: cover;
    width: 95%;
    
}


.client-area {
    text-align: center;
}

.client-area > h1  {
    color: #5FBC44;
    font-weight: 400;
}

.client-area > p {
    font-size: 1.5rem;
}
   
.client-img {
    width: 35rem;
    margin: 0 auto;
}




/* HOME DESIGN */



